import React from 'react';
import styled from 'styled-components';
import './arrowsStyle.css';
import WorkCard from 'components/WorkCard/WorkCard';
import { projects_wide, projects_two_cols, projects_one_col } from './Projects';


const WorksWrapper = styled.div`
    display: flex;
    align-items: space-between;
    position: relative;
    font-family: ${({ theme }) => theme.font.family.nunito};

`;

const Slide = styled.div`
  padding: 0 10vw;
`;

const HeadingBig = styled.h1`
  color: ${({ theme }) => theme.pink};
  font-size: ${({ theme }) => theme.font.size.l};
  font-weight: ${({ theme }) => theme.font.weight.bold};
  letter-spacing: 6px;
  margin: 0 0 10px 0;
  position: absolute;
  top: 15vh;
  left: 10vw;

  ${({ theme }) => theme.mq.wide} {
    font-size: ${({ theme }) => theme.font.size.xl};
  }
`;

function Works() {
  let projects = projects_wide;
  if (window.innerWidth >= 796 && window.innerWidth < 1280) projects = projects_two_cols;
  if (window.innerWidth < 796) projects = projects_one_col;

  return (
    <WorksWrapper>
      <HeadingBig>my works_</HeadingBig>
      {(window.innerWidth >= 796) ? (
        projects.map(projectObj => (
          <Slide key={projectObj.id} className="slide">
              {projectObj.works.map(project => (
                  <WorkCard key={project.id} project={project} />
              ))}
          </Slide>
        ))) : (
          projects.map(project => (
            <Slide key={-project.id} className="slide">
              <WorkCard key={project.id} project={project} />
            </Slide>
        )))
      }
    </WorksWrapper>

  )
}

export default Works;
