import React from 'react';
import styled from 'styled-components';
import gatsby from 'assets/images/gatsby-icon.png';

const ContactWrapper = styled.div`
    display: flex;
    flex-direction: column;
  padding: 0 7vw;
  font-family: ${({ theme }) => theme.font.family.nunito};
`;

const HeadingBig = styled.h1`
  color: ${({ theme }) => theme.pink};
  font-size: ${({ theme }) => theme.font.size.l};
  font-weight: ${({ theme }) => theme.font.weight.bold};
  letter-spacing: 6px;

  ${({ theme }) => theme.mq.wide} {
    font-size: ${({ theme }) => theme.font.size.xl};
  }
`;

const Footer = styled.div`
  position: absolute;
  bottom: 2vh;
  right: 8vw;
`;

const FooterText = styled.p`
  color: ${({ theme }) => theme.secondary};
  font-weight: ${({ theme }) => theme.font.weight.semiBold};
  font-size: ${({ theme }) => theme.font.size.xs};
  margin: 0;
  word-spacing: 3px;
`;

const GatsbyLogo = styled.img`
  height: 18px;
`;

const Contact = () => (
  <ContactWrapper>
    <HeadingBig>contact_me</HeadingBig>
    <Footer>
      <FooterText>made with <GatsbyLogo src={gatsby} alt="gatsby-logo" /> and 🧛 © Grzegorz Olejniczak, 2019</FooterText>
    </Footer>
  </ContactWrapper>
);

export default Contact;
